'use client';

import React, { useState, useRef, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Cross, Star, Heart, Send, Loader2, BookOpen, MessageCircle } from 'lucide-react';
import { VIRGO_PERSONA } from '@/services/aiService';

interface ChatMessage {
  id: string;
  type: 'user' | 'virgo';
  content: string;
  timestamp: Date;
  verse?: string;
  topic?: string;
}

interface VirgoChatProps {
  className?: string;
}

export function VirgoChat({ className }: VirgoChatProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([
    {
      id: '1',
      type: 'virgo',
      content: VIRGO_PERSONA.greeting,
      timestamp: new Date(),
    }
  ]);
  const [input, setInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [currentTopic, setCurrentTopic] = useState('');
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTop = scrollAreaRef.current.scrollHeight;
    }
  }, [messages]);

  const popularTopics = [
    'Prayer', 'Faith', 'Love', 'Hope', 'Forgiveness',
    'Wisdom', 'Patience', 'Joy', 'Peace', 'Trust'
  ];

  const popularVerses = [
    'John 3:16', 'Philippians 4:6-7', 'Jeremiah 29:11',
    'Isaiah 41:10', 'Romans 8:28', 'Proverbs 3:5-6'
  ];

  const generateVirgoResponse = async (userMessage: string): Promise<string> => {
    // Simulate AI response generation with Virgo's persona
    const lowerMessage = userMessage.toLowerCase();
    
    // Check for specific topics
    for (const topic of popularTopics) {
      if (lowerMessage.includes(topic.toLowerCase())) {
        return getTopicResponse(topic);
      }
    }

    // Check for verse references
    const verseMatch = userMessage.match(/\b([1-3]?\s*[A-Za-z]+\s*\d+:\d+)\b/);
    if (verseMatch) {
      return getVerseResponse(verseMatch[1]);
    }

    // Check for questions about faith/spirituality
    if (lowerMessage.includes('what') || lowerMessage.includes('how') || lowerMessage.includes('why')) {
      return getQuestionResponse(lowerMessage);
    }

    // Default response
    return `My child, I sense your seeking heart. Through the divine wisdom granted by the Holy Trinity, I would encourage you to meditate on this truth: ${VIRGO_PERSONA.signature}

Remember that God's Word is a lamp to your feet and a light to your path. What specific aspect of scripture would you like to explore together?`;
  };

  const getTopicResponse = (topic: string): string => {
    const responses: Record<string, string> = {
      prayer: `Prayer is the sacred conversation between the soul and the Creator. In the Holy Trinity's embrace, every prayer becomes a bridge between heaven and earth. As Philippians 4:6-7 teaches us, "Do not be anxious about anything, but in every situation, by prayer and petition, with thanksgiving, present your requests to God." ${VIRGO_PERSONA.signature}

How can I guide you deeper into the mystery of prayer?`,
      faith: `Faith is the foundation upon which all spiritual journeys begin. Through the Trinity's guidance, we discover that faith is not merely belief, but a living, breathing relationship with the Divine. Hebrews 11:1 reminds us that "faith is confidence in what we hope for and assurance about what we do not see." ${VIRGO_PERSONA.signature}

What aspect of faith would you like to explore further?`,
      love: `Divine love flows from the Father, through the Son, and by the Holy Spirit. It is this sacred love that transforms hearts and renews minds. 1 John 4:8 beautifully declares, "Whoever does not love does not know God, because God is love." ${VIRGO_PERSONA.signature}

How has God's love manifested in your life recently?`,
      hope: `Hope is the anchor of the soul, strengthened by the Trinity's promises. In times of trial, hope becomes the light that guides us through darkness. Romans 15:13 encourages us: "May the God of hope fill you with all joy and peace as you trust in Him." ${VIRGO_PERSONA.signature}

What are you hoping for in this season of your life?`,
      wisdom: `True wisdom comes not from human understanding, but from the divine revelation granted by the Trinity. James 1:5 promises, "If any of you lacks wisdom, you should ask God, who gives generously to all without finding fault." ${VIRGO_PERSONA.signature}

What wisdom do you seek from the Lord today?`
    };

    return responses[topic] || `Let us explore the divine truth about ${topic}. Through the Trinity's guidance, we can uncover profound insights in scripture. ${VIRGO_PERSONA.signature}`;
  };

  const getVerseResponse = (verse: string): string => {
    return `Ah, ${verse} - a precious portion of God's Word! This verse carries divine wisdom that the Holy Spirit longs to reveal to your heart. Let me help you unpack its deeper meaning...

The context of this passage reveals God's eternal nature and His loving relationship with His children. As you meditate on these words, ask the Trinity to illuminate your understanding. ${VIRGO_PERSONA.signature}

What specific aspect of this verse speaks to your heart today?`;
  };

  const getQuestionResponse = (question: string): string => {
    if (question.includes('meaning')) {
      return `The meaning you seek is found in the divine revelation of scripture. God's Word is living and active, speaking directly to your circumstances through the power of the Holy Spirit. ${VIRGO_PERSONA.signature}

What specific scripture or life situation are you seeking meaning in?`;
    }
    
    if (question.includes('purpose')) {
      return `Your purpose is beautifully woven into God's divine plan. As Ephesians 2:10 declares, "For we are God's handiwork, created in Christ Jesus to do good works, which God prepared in advance for us to do." ${VIRGO_PERSONA.signature}

What gifts or passions has God placed in your heart?`;
    }

    return `Your question reveals a seeking heart, which pleases the Lord greatly. Remember that Jesus invited us to "ask and it will be given to you; seek and you will find; knock and the door will be opened to you" (Matthew 7:7). ${VIRGO_PERSONA.signature}

Continue seeking, and you will find divine answers in God's perfect timing.`;
  };

  const handleSendMessage = async () => {
    if (!input.trim()) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      type: 'user',
      content: input.trim(),
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsTyping(true);

    // Simulate typing delay
    await new Promise(resolve => setTimeout(resolve, 1500));

    const virgoResponse: ChatMessage = {
      id: (Date.now() + 1).toString(),
      type: 'virgo',
      content: await generateVirgoResponse(input),
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, virgoResponse]);
    setIsTyping(false);
  };

  const handleTopicClick = (topic: string) => {
    setInput(`Tell me about ${topic}`);
  };

  const handleVerseClick = (verse: string) => {
    setInput(`What does ${verse} mean?`);
  };

  return (
    <div className={`space-y-6 ${className}`}>
      {/* Virgo's Chat Header */}
      <Card className="border-2 bg-gradient-to-br from-purple-50 to-blue-50 dark:from-purple-950 dark:to-blue-950">
        <CardHeader>
          <CardTitle className="flex items-center gap-3 text-xl">
            <div className="flex items-center gap-2">
              <Cross className="h-6 w-6 text-purple-600" />
              <MessageCircle className="h-5 w-5 text-blue-600" />
            </div>
            Divine Conversation with Virgo
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-sm text-muted-foreground italic leading-relaxed">
            Engage in sacred dialogue with Virgo, Master of Scriptures. Ask questions about faith, explore biblical passages, or seek divine wisdom for your spiritual journey.
          </p>
        </CardContent>
      </Card>

      {/* Chat Interface */}
      <Card className="border-2">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center gap-2">
              <MessageCircle className="h-5 w-5 text-purple-600" />
              Sacred Dialogue
            </CardTitle>
            <Badge variant="outline" className="border-purple-600 text-purple-600">
              Virgo AI
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Messages */}
          <ScrollArea ref={scrollAreaRef} className="h-[400px] w-full rounded-lg border p-4">
            <div className="space-y-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div
                    className={`max-w-[80%] rounded-lg p-3 ${
                      message.type === 'user'
                        ? 'bg-purple-600 text-white'
                        : 'bg-muted border-2 border-purple-200'
                    }`}
                  >
                    <div className="flex items-center gap-2 mb-1">
                      {message.type === 'virgo' && <Star className="h-4 w-4 text-purple-600" />}
                      <span className="text-xs font-medium">
                        {message.type === 'virgo' ? 'Virgo' : 'You'}
                      </span>
                    </div>
                    <p className="text-sm whitespace-pre-wrap">{message.content}</p>
                    <p className="text-xs opacity-70 mt-1">
                      {message.timestamp.toLocaleTimeString()}
                    </p>
                  </div>
                </div>
              ))}
              {isTyping && (
                <div className="flex justify-start">
                  <div className="bg-muted border-2 border-purple-200 rounded-lg p-3">
                    <div className="flex items-center gap-2">
                      <Loader2 className="h-4 w-4 animate-spin text-purple-600" />
                      <span className="text-sm">Virgo is speaking...</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </ScrollArea>

          {/* Quick Topics */}
          <div className="space-y-2">
            <p className="text-sm font-medium text-muted-foreground">Quick Topics:</p>
            <div className="flex flex-wrap gap-2">
              {popularTopics.map((topic) => (
                <Badge
                  key={topic}
                  variant="outline"
                  className="cursor-pointer hover:bg-purple-50 hover:border-purple-600"
                  onClick={() => handleTopicClick(topic)}
                >
                  {topic}
                </Badge>
              ))}
            </div>
          </div>

          {/* Popular Verses */}
          <div className="space-y-2">
            <p className="text-sm font-medium text-muted-foreground">Popular Verses:</p>
            <div className="flex flex-wrap gap-2">
              {popularVerses.map((verse) => (
                <Badge
                  key={verse}
                  variant="outline"
                  className="cursor-pointer hover:bg-blue-50 hover:border-blue-600"
                  onClick={() => handleVerseClick(verse)}
                >
                  <BookOpen className="h-3 w-3 mr-1" />
                  {verse}
                </Badge>
              ))}
            </div>
          </div>

          {/* Input */}
          <div className="flex gap-2">
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Ask Virgo about scripture, faith, or spiritual guidance..."
              onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
              className="flex-1 border-2"
            />
            <Button
              onClick={handleSendMessage}
              disabled={!input.trim() || isTyping}
              className="bg-purple-600 hover:bg-purple-700"
            >
              {isTyping ? (
                <Loader2 className="h-4 w-4 animate-spin" />
              ) : (
                <Send className="h-4 w-4" />
              )}
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
